import { debounce } from 'throttle-debounce'
import Uitls from '@/services/utils/util'
import ClickOutside from '@/services/directives/click-outside'
import { getCursorPosition, setCursorPosition } from '@/services/utils/dom'
import gravatar from '@/services/gravatar/gravatar'

const emojiList = [
  'üòÉ', 'üòÇ', 'üòÖ', 'üòâ', 'üòå', 'üòî', 'üòì', 'üòò', 'üò°', 'üò≠', 'üò±', 'üò≥', 'üòµ',
  'üåö', 'üëç', 'üëé', 'üí™', 'üåπ', 'üíä'
]

export default {
  directives: {
    ClickOutside
  },
  props: {
    isShowReplyContent: {
      type: Boolean,
      default: false
    },

    isMessageEditor: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: 'ÂçÉÁîüÁôæ‰∏ñÔºåÁºòËµ∑ÁºòÁÅ≠ÔºåÁöÜÂ∑≤Ê≥®ÂÆö...'
    }
  },

  data() {
    return {
      flag: false,
      isShowEmoji: false,
      contentText: '',
      contentHtml: '',
      form: {
        nickname: '',
        content: '',
        email: '',
        website: ''
      },
      reply: {
        replyNickname: '',
        replyContent: ''
      },
      emojiList,
      cursorPosition: 0
    }
  },

  computed: {
    avatar() {
      return gravatar(this.form.email)
    }
  },

  created() {
    // ÂèëÈÄÅÈò≤Êäñ
    this.debouncedSend = debounce(1000, true, () => {
      this.send()
    })
    if (process.client) {
      let user = window.localStorage.getItem('USER')
      if (user) {
        user = JSON.parse(user)
        this.form.nickname = user.nickname || ''
        this.form.email = user.email || ''
        this.form.website = user.website || ''
      }
    }
  },

  methods: {
    // ÈáçÁΩÆ‰∏∫Á©∫
    resetField() {
      this.form.content = ''
      this.contentText = ''
      this.contentHtml = ''
      this.$refs.editContent.innerHTML = ''
      this.$refs.editContent.innerText = ''
      this.cursorPosition = 0
    },

    closeReplyContent() {
      this.$emit('closeReplyContent')
    },

    contentChange() {
      const html = this.$refs.editContent.innerHTML
      const text = this.$refs.editContent.innerText
      if (!Object.is(html, this.comemntContentHtml)) {
        this.contentHtml = html
      }
      if (!Object.is(text, this.comemntContentText)) {
        this.contentText = text
      }
      this.cursorPosition = getCursorPosition(this.$refs.editContent)
    },

    getCursorPosition() {
      this.cursorPosition = getCursorPosition(this.$refs.editContent)
    },

    send() {
      this.contentChange()
      const inValidMsg = this.getInValidMsg()
      if (inValidMsg) {
        this.$message.warning(inValidMsg)
        return
      }
      if (process.client) {
        const user = {
          nickname: this.form.nickname,
          email: this.form.email,
          website: this.form.website
        }
        window.localStorage.setItem('USER', JSON.stringify(user))
      }
      this.$emit('send', this.form)
    },

    getInValidMsg() {
      this.form.content = this.contentText
      if (!this.isMessageEditor) {
        if (!this.form.email) {
          return 'ÈÇÆÁÆ±‰∏çËÉΩ‰∏∫Á©∫'
        }
        if (Uitls.validateEmail(this.form.email) === false) {
          return 'ËØ∑Â°´ÂÜôÊ≠£Á°ÆÁöÑemailÊ†ºÂºè'
        }
      }
      if (this.form.website && Uitls.validateUrl(this.form.website) === false) {
        return 'ËØ∑Â°´ÂÜôÊ≠£Á°ÆÁöÑurlÊ†ºÂºè'
      }
      if (!this.form.content || !this.form.content.replace(/\s/g, '')) {
        if (this.form.content.length > 1000 || this.form.content.split('\n').length > 30) {
          return `${this.isMessageEditor ? 'ÁïôË®Ä' : 'ËØÑËÆ∫'}ÂÜÖÂÆπ‰∏çËÉΩË∂ÖËøá1000‰∏™Â≠ó‰∏îÊéßÂà∂Âú®30Ë°å‰ª•ÂÜÖ`
        }
        return this.isMessageEditor ? 'ÁïôË®ÄÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫' : 'ËØÑËÆ∫ÂÜÖÂÆπ‰∏çËÉΩ‰∏∫Á©∫'
      }
      // ÁïôË®ÄÂ¢ô
      if (this.isMessageEditor) {
        if (this.form.nickname && this.form.nickname.length > 32) {
          return 'ÊòµÁß∞‰∏çËÉΩË∂ÖËøá32‰∏™Â≠óÁ¨¶'
        }
      } else {
        if (!this.form.nickname || this.form.nickname.length > 32) {
          return 'ÊòµÁß∞‰∏çËÉΩ‰∏∫Á©∫‰∏îÈïøÂ∫¶‰∏çËÉΩË∂ÖËøá32‰∏™Â≠óÁ¨¶'
        }
      }
      return false
    },

    insertContent(type) {
      const contents = {
        image: {
          start: `![`,
          end: `]()`
        },
        link: {
          start: `[`,
          end: `]()`
        },
        code: {
          start: '\n```\n',
          end: '\n```'
        }
      }
      if (contents[type]) {
        this.updateEditContent(contents[type])
      }
      this.focusSelection(this.$refs.editContent)
    },
    focusSelection(dom) {
      if (window.getSelection) { // ie11 10 9 ff safari
        dom.focus() // Ëß£ÂÜ≥ff‰∏çËé∑ÂèñÁÑ¶ÁÇπÊó†Ê≥ïÂÆö‰ΩçÈóÆÈ¢ò
        const range = window.getSelection()// ÂàõÂª∫range
        range.selectAllChildren(dom)// range ÈÄâÊã©obj‰∏ãÊâÄÊúâÂ≠êÂÜÖÂÆπ
        range.collapseToEnd()// ÂÖâÊ†áÁßªËá≥ÊúÄÂêé
      } else if (document.selection) { // ie10 9 8 7 6 5
        const range = document.selection.createRange()// ÂàõÂª∫ÈÄâÊã©ÂØπË±°
        range.moveToElementText(dom)// rangeÂÆö‰ΩçÂà∞obj
        range.collapse(false)// ÂÖâÊ†áÁßªËá≥ÊúÄÂêé
        range.select()
      }
    },
    insertEmoji(emoji) {
      const editor = this.$refs.editContent
      const text = editor.innerText
      // ÊèíÂÖ• emoji
      editor.innerText = text.slice(0, this.cursorPosition) + `${emoji}` + text.slice(this.cursorPosition, text.length)
      // ÂÖâÊ†á‰ΩçÁΩÆÂêéÊå™‰∏Ä‰ΩçÔºå‰ª•‰øùËØÅÂú®ÂàöÊèíÂÖ•ÁöÑ emoji ÂêéÈù¢
      setCursorPosition(editor, this.cursorPosition + 2)
      // Êõ¥Êñ∞Êú¨Âú∞‰øùÂ≠òÁöÑÂÖâÊ†á‰ΩçÁΩÆÂèòÈáèÔºàÊ≥®ÊÑè emoji Âç†‰∏§‰∏™Â≠óËäÇÂ§ßÂ∞èÔºåÊâÄ‰ª•Ë¶ÅÂä†1Ôºâ
      this.cursorPosition = getCursorPosition(editor) + 2 // emoji Âç†‰∏§‰Ωç
      this.contentChange()
    },

    updateEditContent({ start = '', end = '' }) {
      if (!start && !end) {
        return
      }
      const editor = this.$refs.editContent
      const selectedText = (window.getSelection || document.getSelection)().toString()
      const currentText = editor.innerText
      if (selectedText) {
        const newText = currentText.replace(selectedText, start + selectedText + end)
        editor.innerText = newText
      } else {
        editor.innerText = editor.innerText += (start + end)
      }
    }
  }
}
